body {
  font-family: Arial, sans-serif;
  text-align: center;
  margin: 20px;
}

.App {
  padding: 20px;
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.controls input[type="text"] {
  padding: 10px;
  width: 200px;
  margin-right: 10px;
}

button {
  padding: 10px;
}

.filter-buttons {
  margin: 10px 0;
}

.filter-buttons .active {
  font-weight: bold;
}

.dark {
  background-color: #333;
  color: white;
}